<template>
  <div
    class="menu-block"
    @mouseenter.self="isCover = false"
    @mouseleave.self="isCover = true"
  >
    <transition name="cover">
      <div
        class="cover"
        v-show="isCover"
        :pic="pic"
        :style="{ 'background-Color': pic.color }"
      >
        <div class="title">{{ pic.name }}</div>
      </div>
    </transition>
    <img :src="pic.url" class="img" />
  </div>
</template>

<script>
export default {
  props: ["pic"],
  data() {
    return {
      isCover: true,
    };
  },
  mounted() {},
};
</script>

<style scoped>
.menu-block {
  position: relative;
}
.img {
  top: 0;
  left: 0;
  width: 100%;
  display: block;
}
.cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 9.3rem;
  z-index: 1;
  opacity: 0.8;
}

.cover .title {
  color: rgb(245, 223, 77);
  font-size: 38px;
  height: 1em;
  text-align: center;
  line-height: 1em;
}
.cover-enter {
  opacity: 0;
}
.cover-enter-active {
  transition: all 0.5s;
}
.cover-leave {
  opacity: 0.5;
}
.cover-leave-active {
  transition: all 0.5s;
}
.cover-leave-to {
  opacity: 0;
}
</style>