<template>
  <div class="header">
    <transition name="img">
      <div
        class="img"
        v-show="show"
        ref="img"
        @mousemove="imgMove($event, $el)"
        @mouseleave="imgInit($el)"
      ></div>
    </transition>
  </div>
</template>

// <script>
// console.log(111);
export default {
  data() {
    return {
      show: false,
      isMove: false,
      oldX: 0,
      oldY: 0,
      timer: null,
    };
  },
  methods: {
    imgMove(e, el) {
      if (!this.isMove) {
        return;
      }
      const img = el.children[0];
      let X = e.pageX - this.oldX,
        Y = e.pageY - this.oldY;
      this.oldX = e.pageX;
      this.oldY = e.pageY;

      if (Y >= 0) {
        img.style.backgroundPositionY =
          parseFloat(getComputedStyle(img).backgroundPositionY) - 1 / 20 + "%";
      } else if (Y < 0) {
        img.style.backgroundPositionY =
          parseFloat(getComputedStyle(img).backgroundPositionY) + 1 / 20 + "%";
      }
      if (X >= 0) {
        img.style.backgroundPositionX =
          parseFloat(getComputedStyle(img).backgroundPositionX) - 1 / 20 + "%";
      } else if (X < 0) {
        img.style.backgroundPositionX =
          parseFloat(getComputedStyle(img).backgroundPositionX) + 1 / 20 + "%";
      }
      //   img.style.backgroundPositionY =
      //       parseFloat(getComputedStyle(img).backgroundPositionY) -
      //       e.pageY / 80 +
      //       "px";
    },
    imgInit(el) {
      const img = el.children[0];
      const self = this;
      this.timer = setInterval(() => {
        if (self.timer == null) {
          return;
        }
        if (
          getComputedStyle(img).backgroundPositionX == "50%" &&
          getComputedStyle(img).backgroundPositionY == "50%"
        ) {
          clearInterval(self.timer);
          self.timer = null;
          return;
        }
        if (parseFloat(getComputedStyle(img).backgroundPositionX) >= 50) {
          img.style.backgroundPositionX =
            parseFloat(getComputedStyle(img).backgroundPositionX) -
            1 / 20 +
            "%";
        } else if (parseFloat(getComputedStyle(img).backgroundPositionX) < 50) {
          img.style.backgroundPositionX =
            parseFloat(getComputedStyle(img).backgroundPositionX) +
            1 / 20 +
            "%";
        }
        if (parseFloat(getComputedStyle(img).backgroundPositionY) >= 50) {
          img.style.backgroundPositionY =
            parseFloat(getComputedStyle(img).backgroundPositionY) -
            1 / 20 +
            "%";
        } else if (parseFloat(getComputedStyle(img).backgroundPositionY) < 50) {
          img.style.backgroundPositionY =
            parseFloat(getComputedStyle(img).backgroundPositionY) +
            1 / 20 +
            "%";
        }
        if (
          Math.abs(
            parseFloat(getComputedStyle(img).backgroundPositionX) - 50
          ) <= 0.5 &&
          Math.abs(
            parseFloat(getComputedStyle(img).backgroundPositionY) - 50
          ) <= 0.5
        ) {
          img.style.backgroundPositionY = "50%";
          img.style.backgroundPositionX = "50%";
        }
      }, 16);
    },
  },
  mounted() {
    let self = this;
    this.show = true;
    setInterval(() => {
      self.isMove = true;
    }, 700);
  },
};
</script>

<style lang='less' scoped>
.header {
  width: 100%;
}
.img {
  background-image: url("~@/imgs/topImg/1.jpg");
  background-size: 120%;
  height: 100em;
  width: 100%;
  background-position-x: 50%;
  background-position-y: 50%;
  color: red;
}
.img-enter {
  background-position-y: 0%;
}
.img-enter-active {
  /* background-position-y: 50%; */
  transition: all 0.5s;
}

</style>